import { showLoadError } from "../errors";
import {
  MessageKind,
  showAfterUpdatePageMessage,
  showPageMessage,
} from "../alert";
import { RegisterDeviceResponse, StateData, StaticData } from "../data/types";
import { hideGlobalSpinner, showGlobalSpinner } from "../spinner";
import { getBaseUrl } from "../help";

const MF_REGISTER_FORM_ID = "mf-register-form";
const REGISTRATION_DEV_SBMT_BTN_ID = "registration-device-btn";
const REGISTRATION_FORM_ID = "mf-register-form";
const URL = `${getBaseUrl()}/api/mf_reg.json`;

export function initRegisterDevice(
  container: HTMLElement,
  data: StaticData,
  stateData: StateData
): void {
  const form =
    (container.querySelector(`#${REGISTRATION_FORM_ID}`) as HTMLFormElement) ||
    null;
  if (!form || !form.parentElement?.classList.contains("show")) {
    return;
  }
  checkInternetConnection(stateData.inetState, form);
  checkWireguardConnection(stateData.wgState);
  fillForm(container, data);
  rigSubmitForm(container);
}

function checkInternetConnection(
  inetState: number,
  form: HTMLFormElement
): void {
  if (inetState) {
    return;
  }
  showPageMessage(
    `No internet access. Active internet connection is required to restore the device's identity. 
    Please check the device's settings.`,
    MessageKind.Danger
  );
  form
    .querySelector(`#${REGISTRATION_DEV_SBMT_BTN_ID}`)!
    .setAttribute("disabled", "disabled");
}

function checkWireguardConnection(wgState: number): void {
  if (wgState === 3) {
    showPageMessage(
      `This device is already authenticated on the remote server and has an access. 
      By submitting this form you may lose the access or change the device's identity.`,
      MessageKind.Warning
    );
  }
}

function fillForm(container: HTMLElement, data: StaticData): void {
  const form = container.querySelector(
    `#${MF_REGISTER_FORM_ID}`
  ) as HTMLFormElement;

  (form.querySelector("#license-id") as HTMLInputElement).value =
    data.mfLicenseUuid;
  (form.querySelector("#license-password") as HTMLInputElement).value =
    data.mfLicensePassword;
  (form.querySelector("#registration-description") as HTMLInputElement).value =
    data.mfBatchDescription;
  (form.querySelector("#public-key") as HTMLInputElement).value =
    data.devKeyPub;
  (form.querySelector("#mf-device-mac") as HTMLInputElement).value =
    data.devMac;
}

function rigSubmitForm(container: HTMLElement): void {
  const form = container.querySelector(
    `#${MF_REGISTER_FORM_ID}`
  ) as HTMLFormElement;
  form.addEventListener("submit", async (e) => {
    e.preventDefault();
    const conf = confirm("Do you want to register device?");
    if (!conf) {
      return;
    }
    await submit(form);
  });
}

async function submit(form: HTMLFormElement): Promise<void> {
  hideFormError(form);

  showGlobalSpinner(true);

  const licenceId = (form.querySelector("#license-id") as HTMLInputElement)
    .value;
  const licencePass = (
    form.querySelector("#license-password") as HTMLInputElement
  ).value;
  const description = (
    form.querySelector("#registration-description") as HTMLTextAreaElement
  ).value;

  try {
    const resp = JSON.parse(
      await (
        await fetch(
          `${URL}?license_uuid=${licenceId}&license_password=${licencePass}&batch_description=${description}`
        )
      ).text()
    ) as RegisterDeviceResponse;
    if (Number(resp.error_code)) {
      showPageMessage(
        "Form submit error. Please try again.",
        MessageKind.Danger
      );
      showFormError(
        form,
        resp.error_descr || "Undefined error. Please try again."
      );
      hideGlobalSpinner();
      return;
    } else {
      showAfterUpdatePageMessage(
        `The device has successfully registered on the remote server. Please print out the label image file 
        to be able to restore the device's identity in the future:
        <a href="./opcb-label.png" target="_blank">Image</a>`,
        MessageKind.Success
      );
    }

    sessionStorage.setItem("isReboot", "true");
  } catch (e) {
    const text = (e as Error).message;
    showLoadError(text);
    return;
  }
}

function showFormError(form: HTMLFormElement, errorText: string): void {
  form.querySelector("#register-device-error")!.innerHTML = errorText;
}

function hideFormError(form: HTMLFormElement): void {
  form.querySelector("#register-device-error")!.innerHTML = "";
}
