import { setNewValue } from ".";
import { StateData, SettingsData, WifiModes } from "../../data/types";

enum WiFiStatus {
  disabled = "Disabled",
  invalid = "Invalid Credentials",
  offline = "Offline",
  online = "Online",
  accessPoint = "Access Point",
}

export function updateWiFi(data: StateData): void {
  updateWiFiStatus(data.wlanEnabled, data.wlanMode, data.wlanState);
  updateWiFiIp(data.wlanIp, data.wlanGateway, data.wlanMaskPrefix);
  updateAccessPointAddress(data.wlanMode);
}

function updateWiFiStatus(isEnable: number, mode: number, state: number): void {
  const el = document.querySelector("#wlan-status");
  if (!el) {
    return;
  }

  if (!isEnable) {
    setNewValue(el.querySelector(".value")!, WiFiStatus.disabled);
    return;
  }

  if (mode === WifiModes.AccessPoint) {
    setNewValue(el.querySelector(".value")!, WiFiStatus.accessPoint);
    return;
  }

  if (state === 0) {
    setNewValue(el.querySelector(".value")!, WiFiStatus.invalid);
    return;
  } else if (state === 1 || state == 2) {
    setNewValue(el.querySelector(".value")!, WiFiStatus.offline);
    return;
  }

  setNewValue(el.querySelector(".value")!, WiFiStatus.online);
}

function updateWiFiIp(
  wlanIp: string,
  wlanGateway: string,
  wlanMaskPrefix: number
): void {
  const el = document.querySelector("#wlan-ip");
  if (!el) {
    return;
  }
  setNewValue(
    el.querySelector(".value")!,
    `${wlanIp}/${wlanMaskPrefix} (gate: ${wlanGateway})`
  );
}

function updateAccessPointAddress(mode: WifiModes): void {
  const el = document.querySelector("#wlan-access-point-address");
  if (!el || mode !== WifiModes.AccessPoint) {
    return;
  }
  if (mode !== WifiModes.AccessPoint) {
    el.classList.add("d-none");
  }
  el.classList.remove("d-none");
}
