import { showGlobalSpinner } from "../spinner";
import { getBaseUrl } from "../help";

const REBOOT_DEVICE_BTN_ID = "reboot-device-btn";
const REBOOT_URL = `${getBaseUrl()}/api/cmd.json?cmd_abort_all`;

export function rigRebootDevice(): void {
  document
    .querySelector(`#${REBOOT_DEVICE_BTN_ID}`)
    ?.addEventListener("click", async () => {
      const conf = confirm("Do you want to reboot device?");
      if (!conf) {
        return;
      }
      await rebootDevice();
    });
}

async function rebootDevice(): Promise<void> {
  showGlobalSpinner(true);
  sessionStorage.setItem("isReboot", "true");

  await fetch(REBOOT_URL);
}
