import { rigDropdownMenu } from "./dropdown";
import {
  checkSavedTabName,
  rigSaveSelectedTabToSession,
  TAB_PANEL_ID,
} from "./select-tab";

export function initTabPanel(): void {
  if (!document.querySelector(`#${TAB_PANEL_ID}`)) {
    return;
  }
  rigDropdownMenu();
  rigSaveSelectedTabToSession();
  checkSavedTabName();
}
