import { WifiModes, WifiSsidList } from "../../data/types";
import iso from "iso-3166-1";
import { DHCPModes } from "../../data/types";
import { loadListNif, loadWifiSsidNames } from "../../data/load";
import { ControlLoadError } from "../../errors";
import { fillSelect } from "../../help";

const WIFI_MODE_ID = "wifi-mode";
const CLIENT_SETT_ID = "wifi-client-settings";
const POINT_SETT_ID = "wifi-point-settings";
const WIFI_DHCP_MODE_ID = "wifi-dhcp-mode";
const DHCP_CUSTOM_SETT_ID = "wifi-dhcp-custom-sett";
const WIFI_ISO_SELECT_ID = "wifi-country-code";
const WIFI_NETIF_SELECT_ID = "wifi-netif-name";
const WIFI_SSID_SELECT_ID = "wifi-sta-ssid";
const WIFI_SSID_UPDATE_BTN_ID = "wifi-ssid-update-btn";
const WIFI_NETIF_UPDATE_BTN_ID = "wifi-netif-update-btn";

export function rigWifiChanges(): void {
  rigChangeWifiMode();
  rigChangeWifiDHCP();
}

function rigChangeWifiMode(): void {
  const select = document.querySelector(
    `#${WIFI_MODE_ID}`
  ) as HTMLSelectElement;
  const clientSett = document.querySelector(`#${CLIENT_SETT_ID}`);
  const pointSett = document.querySelector(`#${POINT_SETT_ID}`);

  select.addEventListener("change", () => {
    const value = Number(select.value) as WifiModes;
    if (value === WifiModes.Disabled) {
      clientSett?.classList.add("d-none");
      pointSett?.classList.add("d-none");
    } else if (value === WifiModes.AccessPoint) {
      clientSett?.classList.add("d-none");
      pointSett?.classList.remove("d-none");
    } else if (value === WifiModes.Client) {
      clientSett?.classList.remove("d-none");
      pointSett?.classList.add("d-none");
    }
  });
}

function rigChangeWifiDHCP(): void {
  const select = document.querySelector(
    `#${WIFI_DHCP_MODE_ID}`
  ) as HTMLSelectElement;

  const customSettEl = document.querySelector(`#${DHCP_CUSTOM_SETT_ID}`);

  select.addEventListener("change", () => {
    const value = Number(select.value) as DHCPModes;
    if (value === DHCPModes.Automatic) {
      customSettEl?.classList.add("d-none");
      return;
    }
    customSettEl?.classList.remove("d-none");
  });
}

export function rigUpdateWifiSsidList(): void {
  const updateBtn = document.querySelector(
    `#${WIFI_SSID_UPDATE_BTN_ID}`
  ) as HTMLElement;

  updateBtn.addEventListener("click", async () => {
    updateBtn.setAttribute("disabled", "disabled");
    updateBtn.querySelector(".bi-arrow-clockwise")?.classList.add("d-none");
    updateBtn.querySelector(".spinner-border")?.classList.remove("d-none");
    await fillWifiSsidNames();
    updateBtn.removeAttribute("disabled");
  });
}

export function rigUpdateWifiNetifList(): void {
  const updateBtn = document.querySelector(
    `#${WIFI_NETIF_UPDATE_BTN_ID}`
  ) as HTMLElement;
  updateBtn.addEventListener("click", async () => {
    updateBtn.querySelector(".bi-arrow-clockwise")?.classList.add("d-none");
    updateBtn.querySelector(".spinner-border")?.classList.remove("d-none");
    await fillWifiNetifList();
    updateBtn.querySelector(".spinner-border")?.classList.add("d-none");
    updateBtn.querySelector(".bi-arrow-clockwise")?.classList.remove("d-none");
  });
}

// Poland by default
export function fillWifiISOSelect(defIso: string = "616"): void {
  const list = iso.all();
  const selectEl = document.querySelector(
    `#${WIFI_ISO_SELECT_ID}`
  ) as HTMLSelectElement;

  for (const country of list) {
    const opEl = document.createElement("option");
    opEl.value = country.numeric;
    opEl.text = country.country;
    if (defIso === country.numeric) {
      opEl.selected = true;
    }
    selectEl.appendChild(opEl);
  }
}

export async function fillWifiNetifList(): Promise<void> {
  const errorControl = new ControlLoadError();
  const list = await loadListNif(errorControl);

  fillSelect(
    WIFI_NETIF_SELECT_ID,
    list.map((i) => i.devName)
  );
}

export async function fillWifiSsidNames(): Promise<void> {
  const errorControl = new ControlLoadError();
  const list = (await loadWifiSsidNames(errorControl)).sort((a, b) =>
    a.signal < b.signal ? 1 : -1
  );

  fillWifiSsidSelect(WIFI_SSID_SELECT_ID, list);

  const updateBtn = document.querySelector(
    `#${WIFI_SSID_UPDATE_BTN_ID}`
  ) as HTMLElement;
  updateBtn.querySelector(".bi-arrow-clockwise")?.classList.remove("d-none");
  updateBtn.querySelector(".spinner-border")?.classList.add("d-none");
}

export function fillWifiSsidSelect(
  selectId: string,
  list: WifiSsidList[]
): void {
  const listEl = document.querySelector(`#${selectId}`) as HTMLSelectElement;
  const currentSsid = listEl.value;

  listEl.innerHTML = "";

  const defaultOption = document.createElement("option");
  defaultOption.value = "";
  defaultOption.text = "----";
  listEl.appendChild(defaultOption);

  const addOption = (ssid: string, signal?: number) => {
    const option = document.createElement("option");
    option.value = ssid;
    option.text = signal ? `${ssid} (${signal} dBm)` : ssid;
    option.selected = ssid === currentSsid;
    listEl.appendChild(option);
  };
  const ssids = list.map((l) => l.ssid);
  if (!ssids.includes(currentSsid)) {
    addOption(currentSsid);
  }

  for (const item of list) {
    addOption(item.ssid, item.signal);
  }
}
