import { formatSizeUnits } from "../../help";
import { StateData } from "../../data/types";
import { setNewValue } from ".";

enum WiFiStatus {
  disabled = "Disabled",
  unaccessible = "Unaccessible",
  offline = "Offline",
  online = "Online",
}

export function updateWireguard(data: StateData): void {
  updateWgState(data.wgState);
  updateWgRxTx(data.wgRxTotal, data.wgTxTotal);
}

function updateWgState(wgState: number): void {
  const el = document.querySelector("#wireguard-status");
  if (!el) {
    return;
  }

  let state = WiFiStatus.disabled;
  if (wgState === 1) {
    state = WiFiStatus.unaccessible;
  } else if (wgState === 2) {
    state = WiFiStatus.offline;
  } else if (wgState === 3) {
    state = WiFiStatus.online;
  }

  setNewValue(el.querySelector(".value")!, state);
}

function updateWgRxTx(wgRxTotal: number, wgTxTotal: number): void {
  const el = document.querySelector("#wireguard-rx-tx");
  if (!el) {
    return;
  }

  setNewValue(
    el.querySelector(".value")!,
    `RX ${formatSizeUnits(wgRxTotal)} total | TX ${formatSizeUnits(
      wgTxTotal
    )} total`
  );
}
