import { setNewValue } from ".";
import { StateData } from "../../data/types";

export enum InetStatus {
  offline = "Offline",
  online = "Online",
}

export function updateInternet(data: StateData): void {
  updateStatus(data.inetState);
}

function updateStatus(inetState: number): void {
  const el = document.querySelector("#internet-status");
  if (!el) {
    return;
  }

  let status = InetStatus.online;
  if (!inetState) {
    status = InetStatus.offline;
  }
  setNewValue(el.querySelector(".value")!, status);
}
