export function getServerPathByServerName(serverName: string): string {
  let serverHost = "ocp.overvis.com";
  if (serverName !== "modbus.overvis.com") {
    serverHost = serverName;
  }

  return `https://${serverHost}`;
}

export function formatSizeUnits(bytes: number): string {
  let result: string = "";
  if (Number(bytes) >= 1073741824) {
    result = (Number(bytes) / 1073741824).toFixed(2) + " gB";
  } else if (Number(bytes) >= 1048576) {
    result = (Number(bytes) / 1048576).toFixed(2) + " MB";
  } else if (Number(bytes) >= 1024) {
    result = (Number(bytes) / 1024).toFixed(2) + " kB";
  } else {
    result = Number(bytes).toFixed(2) + " B";
  }
  return result;
}

export function getFormValues<T>(form: HTMLFormElement): T {
  const fields = Array.from(form.querySelectorAll("[data-holds]"));
  const data: T = {} as T;
  for (const field of fields) {
    const name = field.attributes.getNamedItem("data-holds")!.value;
    if (data[name] && !Array.isArray(data[name])) {
      const val = data[name];
      data[name] = [val];
    }

    let value;
    if (field instanceof HTMLInputElement && field.type === "checkbox") {
      value = field.checked ? 1 : 0;
    } else if (
      field instanceof HTMLInputElement ||
      field instanceof HTMLSelectElement
    ) {
      value = field.value;
    }

    if (Array.isArray(data[name])) {
      data[name].push(value);
    } else {
      data[name] = value;
    }
  }

  return data;
}

export function numberToHex8(value: number): string {
  return value.toString(16).padStart(2, "0");
}

export function numberToHex16(value: number): string {
  return `${numberToHex8(value >> 8)}:${numberToHex8(value & 255)}`;
}

export async function delay(ms: number): Promise<void> {
  return new Promise<void>((resolve) => window.setTimeout(resolve, ms));
}

export function getBaseUrl(): string {
  return window.location.hostname === "localhost"
    ? // "http://staging2.overvis.com"
      "http://10.42.1.3"
    : // ? "http://192.168.31.240"
      "";
}

export function isValidIp(ip: string): boolean {
  return /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/.test(
    ip
  );
}

export function fillSelect(selectId: string, values: string[]): void {
  const listEl = document.querySelector(`#${selectId}`) as HTMLSelectElement;
  const value = listEl.value;

  listEl.innerHTML = "";

  const defaultOption = document.createElement("option");
  defaultOption.value = "";
  defaultOption.text = "----";
  listEl.appendChild(defaultOption);

  const addOption = (val: string) => {
    const option = document.createElement("option");
    option.value = val;
    option.text = val;
    option.selected = val === value;
    listEl.appendChild(option);
  };

  if (!values.includes(value)) {
    addOption(value);
  }

  for (const item of values) {
    addOption(item);
  }
}

export function secondsToDhms(sec: number): string {
  var d = Math.floor(sec / (3600 * 24));
  var h = Math.floor((sec % (3600 * 24)) / 3600);
  var m = Math.floor((sec % 3600) / 60);
  var s = Math.floor(sec % 60);
  return `${d}d ${h}h ${m}m ${s}s`;
}

export function removeDuplicateUidsRanges(value: string): string {
  return value
    .split(",")
    .map((r) => {
      const uids = r.split("-").map((u) => u.trim());
      return uids[0] === uids[1] ? uids[0] : r;
    })
    .join(",");
}

export function rigChangeFields(form: HTMLFormElement): void {
  const elems = Array.from(form.querySelectorAll("[data-holds]"));
  for (const el of elems) {
    el.addEventListener("change", () => {
      el.classList.add("chenged");
    });
  }
}
