import { loadStaticData } from "../data/load";
import { ControlLoadError } from "../errors";

export async function setStaticData(
  errorControl: ControlLoadError
): Promise<void> {
  const data = await loadStaticData(errorControl);

  setFwVersion(data.ver, data.verCommit);
  setPrivateKey(data.devKey);
  setPublicKey(data.devKeyPub);
  setMac(data.devMac);
}

function setFwVersion(fwVer: string, verCommit: string): void {
  const els = Array.from(document.querySelectorAll(".device-version"));
  for (const el of els) {
    el.innerHTML = verCommit ? `${fwVer} (${verCommit})` : fwVer;
  }
}

function setPrivateKey(key: string): void {
  const el = document.querySelector("#wg-private-key");
  if (!el) {
    return;
  }

  el.querySelector(".value")!.innerHTML = `${key.slice(
    0,
    30
  )}<wbr />${key.slice(30)}`;
}

function setPublicKey(publicKey: string): void {
  const el = document.querySelector("#wg-public-key");
  if (!el) {
    return;
  }

  el.querySelector(".value")!.innerHTML = `${publicKey.slice(
    0,
    30
  )}<wbr />${publicKey.slice(30)}`;
}

function setMac(mac: string): void {
  const el = document.querySelector("#device-mac-value");
  if (!el) {
    return;
  }

  el.querySelector(".value")!.innerHTML = mac;
}
