import { showGlobalSpinner } from "../spinner";
import { getBaseUrl } from "../help";

const RESET_DEVICE_BTN_ID = "reset-device-btn";
const RESET_URL = `${getBaseUrl()}/api/cmd.json?cmd_conf_def`;

export function rigResetDevice(): void {
  document
    .querySelector(`#${RESET_DEVICE_BTN_ID}`)
    ?.addEventListener("click", async () => {
      const conf = confirm("Do you want reset the device?");
      if (!conf) {
        return;
      }
      await resetDevice();
    });
}

async function resetDevice(): Promise<void> {
  showGlobalSpinner(true);
  sessionStorage.setItem("isReboot", "true");

  await fetch(RESET_URL);
}
