import {
  ControlLoadError,
  showDisconnectError,
  showLoadError,
} from "../../errors";
import {
  MessageKind,
  showAfterUpdatePageMessage,
  showPageMessage,
} from "../../alert";
import { ErrorResponse, snakeCaseNamesByCamelCaseMap } from "../../data/types";
import { selectTabByTabName, TabNames } from "../../tabpanel/select-tab";
import { validateForm } from "./validate";
import { getBaseUrl, isValidIp } from "../../help";
import { loadSettingsData } from "../../data/load";
import { hideGlobalSpinner, showGlobalSpinner } from "../../spinner";

const SUCCESS_SUBMIT_MSG_TEXT = "Settings saved success.";

const URL = `${getBaseUrl()}/api/set_cnfs.json`;
// const SAVE_FILE_URL = "/api/cmd_conf_c2f";

export function rigSubmitForm(form: HTMLFormElement): void {
  validateForm(form)
    .onSuccess(async (e) => {
      e!.preventDefault();
      const errorControl = new ControlLoadError();
      const settings = await loadSettingsData(errorControl);

      let result:
        | undefined
        | { wlanStaIp?: string; elanIp?: string }
        | ErrorResponse;
      try {
        result = await submit(form);
      } catch (err) {
        showLoadError((err as Error).message);
        return;
      }

      if (result && "error_descr" in result) {
        showPageMessage(
          `Form submit error: ${result.error_descr}`,
          MessageKind.Danger
        );
        return;
      }

      showAfterUpdatePageMessage(SUCCESS_SUBMIT_MSG_TEXT, MessageKind.Success);

      if (
        isValidIp(window.location.hostname) &&
        (result?.elanIp || result?.wlanStaIp)
      ) {
        if (
          window.location.hostname === settings.wlanStaIp &&
          result.wlanStaIp
        ) {
          redirectToNewHostProccess(
            `http://${result.wlanStaIp}${window.location.pathname}`
          );
          return;
        }
        redirectToNewHostProccess(
          `http://${result.elanIp}${window.location.pathname}`
        );
        return;
      }

      window.location.reload();
    })
    .onFail((fields) => {
      for (const key in fields) {
        const fieldItem = fields[key];
        if (!fieldItem.isValid) {
          // show tab with field's error
          selectTabByTabName(
            fieldItem.elem.getAttribute("data-tab-name") as TabNames
          );
          break;
        }
      }
    });
}

async function submit(
  form: HTMLFormElement
): Promise<
  ErrorResponse | { wlanStaIp?: string; elanIp?: string } | undefined
> {
  const fields = Array.from(form.querySelectorAll("[data-holds].chenged"));
  const snakeCaseNamesMap = snakeCaseNamesByCamelCaseMap();

  let wlanStaIp;
  let elanIp;

  const params = [];
  for (const field of fields) {
    const name = field.attributes.getNamedItem("data-holds")!.value;
    const snakeCaseName = snakeCaseNamesMap.get(name);
    if (!snakeCaseName) {
      continue;
    }

    if (field instanceof HTMLInputElement && field.type === "checkbox") {
      const value = field.checked ? 1 : 0;
      params.push(`${snakeCaseName}=${value}`);
    } else if (
      field instanceof HTMLInputElement ||
      field instanceof HTMLSelectElement
    ) {
      params.push(`${snakeCaseName}=${field.value}`);
    }
    if (name === "wlanStaIp") {
      wlanStaIp = (field as HTMLInputElement).value;
    }
    if (name === "elanIp") {
      elanIp = (field as HTMLInputElement).value;
    }
  }

  if (params.length < 1) {
    return;
  }

  const resp = await fetch(URL, {
    method: "POST",
    headers: {
      "Content-Type": "application/x-www-form-urlencoded",
    },
    body: params.join("&"),
  });

  if (resp.body && "error_descr" in resp.body) {
    return resp.body as ErrorResponse;
  }

  return {
    wlanStaIp,
    elanIp,
  };
}

function redirectToNewHostProccess(newHostname: string): void {
  showGlobalSpinner(true);
  const interval = setInterval(async () => {
    const resp = await fetch(getBaseUrl() + "/api/get_sta.json");
    if (resp.status === 200) {
      clearInterval(interval);
      window.location.href = newHostname;
      return;
    }
  }, 1000);

  setTimeout(() => {
    if (interval) {
      hideGlobalSpinner();
      showDisconnectError();
      clearInterval(interval);
    }
  }, 60000);
}
