import { removeDuplicateUidsRanges, secondsToDhms } from "../../help";
import { StateData } from "../../data/types";
import { setNewValue } from ".";

export function updateGeneralTable(stateData: StateData): void {
  updateUptime(stateData.osRunTime);
  updateRuntime(stateData.runtimeStartTimestamp, stateData.osCurTime);
  updateUsedRam(stateData.sysMemUsedB, stateData.sysMemTotalB);
  updateUsedStorage(stateData.sysVolUsedB, stateData.sysVolTotalB);
  updateUidsRanges(stateData.viuRoute);
}

function updateUptime(sec: number): void {
  const el = document.querySelector("#uptime-value");
  if (!el) {
    return;
  }
  setNewValue(el.querySelector(".value")!, secondsToDhms(sec));
}

function updateRuntime(sec: number, osCurTime: number): void {
  const el = document.querySelector("#runtime-value");
  if (!el) {
    return;
  }

  const ms = sec
    ? (new Date(osCurTime * 1000).getTime() - new Date(sec * 1000).getTime()) /
      1000
    : 0;
  setNewValue(el.querySelector(".value")!, secondsToDhms(ms));
}

function updateUsedRam(memeUsedB: number, memTotalB: number): void {
  const el = document.querySelector("#used-ram");
  if (!el) {
    return;
  }

  setNewValue(
    el.querySelector(".value")!,
    `${(memeUsedB / 1024).toFixed(0)}/${(memTotalB / 1024).toFixed(0)}`
  );
}

function updateUsedStorage(volUsedB: number, volTotalB: number): void {
  const el = document.querySelector("#used-storage");
  if (!el) {
    return;
  }

  setNewValue(
    el.querySelector(".value")!,
    `${(volUsedB / 1024).toFixed(0)}/${(volTotalB / 1024).toFixed(0)}`
  );
}

function updateUidsRanges(uids: string): void {
  const el = document.querySelector("#general-uids-ranges");
  if (!el) {
    return;
  }
  setNewValue(el.querySelector(".value")!, removeDuplicateUidsRanges(uids));
}
