import "bootstrap/dist/js/bootstrap.min.js";
import { initState } from "./state";
import { initSettings } from "./settings";
import { initActions } from "./actions";
import { initMenu } from "./menu";
import { hideGlobalSpinner, showGlobalSpinner } from "./spinner";
import { initModbus } from "./modbus";
import { initManufacturing } from "./manufacturing";
import { initTabPanel } from "./tabpanel";
import { checkSavedPageMessage } from "./alert";
import { initQuickSetup } from "./quick-setup";

// TODO: add modbus client 1

window.onload = async () => {
  showGlobalSpinner();
  if (window.location.pathname.startsWith("/quick-start")) {
    await initQuickSetup();
    hideGlobalSpinner();
    return;
  }
  initMenu();
  initTabPanel();
  initActions();
  await initState();
  await initSettings();
  await initModbus();
  await initManufacturing();
  checkSavedPageMessage();
  hideGlobalSpinner();
};
