import { showGlobalSpinner } from "../spinner";
import { getBaseUrl } from "../help";

const BTN_ID = "disconnect-btn";
const URL = `${getBaseUrl()}/api/cmd.json?cmd_ovb_disconnect`;

export function rigDisconnect(): void {
  document.querySelector(`#${BTN_ID}`)?.addEventListener("click", async () => {
    const conf = confirm("Do you want disconnect OPCB-221 from cloud?");
    if (!conf) {
      return;
    }
    await disconnect();
  });
}

async function disconnect(): Promise<void> {
  showGlobalSpinner(true);
  sessionStorage.setItem("isReboot", "true");

  await fetch(URL);
}
