import { showLoadError } from "../errors";
import { MessageKind, showPageMessage } from "../alert";
import {
  CheckDeviceTest,
  CheckDeviceTestResponse,
  StartDeviceTestResponse,
  StateData,
} from "../data/types";
import { getBaseUrl } from "../help";
import * as moment from "moment";

const RUN_TESTS_CONTAINER_ID = "test-device-container";
const RUN_TESTS_BTN_ID = "run-tests-btn";
const ABORT_TESTS_BTN_ID = "abort-run-tests-btn";
const TESTS_LOG_CONTINER_ID = "tests-log-container";

const URL = `${getBaseUrl()}/api/mf_test.json`;

export function initTestDevice(
  container: HTMLElement,
  stateData: StateData
): void {
  const testContainer = container.querySelector(
    `#${RUN_TESTS_CONTAINER_ID}`
  ) as HTMLElement;
  if (
    !testContainer ||
    !testContainer.parentElement?.classList.contains("show")
  ) {
    return;
  }
  checkInternetConnection(testContainer, stateData.inetState);
  checkWireguardConnection(testContainer, stateData.wgState);
  rigRunTests(testContainer);
}

function checkInternetConnection(
  container: HTMLElement,
  inetState: number
): void {
  if (inetState) {
    return;
  }
  showPageMessage(
    `No internet access. Active internet connection is required to run tests. 
      Please check the device's settings.`,
    MessageKind.Danger
  );
  container
    .querySelector(`#${RUN_TESTS_BTN_ID}`)!
    .setAttribute("disabled", "disabled");
}

function checkWireguardConnection(
  container: HTMLElement,
  wgState: number
): void {
  if (wgState !== 3) {
    showPageMessage(
      `Device has no access to the remote server for testing. Please register the device first, 
      or restore its identity.`,
      MessageKind.Danger
    );
    container
      .querySelector(`#${RUN_TESTS_BTN_ID}`)!
      .setAttribute("disabled", "disabled");
  }
}

function rigRunTests(container: HTMLElement): void {
  const btn = container.querySelector(`#${RUN_TESTS_BTN_ID}`)!;
  btn.addEventListener("click", async () => {
    try {
      await runTests(container);
    } catch (e) {
      showLoadError((e as { errorMessage: string }).errorMessage);
    }
  });
}

async function runTests(container: HTMLElement): Promise<void> {
  container.classList.add("test-run");

  // set run tests date
  container.querySelector("#run-test-date")!.innerHTML = moment(
    new Date()
  ).format("YYYY-MM-DD HH:mm:ss");

  // start test and get testRunId
  const testRunId = (
    (await (await fetch(URL)).json()) as StartDeviceTestResponse
  ).testRunId;

  // check device tests
  const statuses: ("waiting" | "running" | "passed" | "failed" | "aborted")[] =
    [];
  let testCount = 1;
  await testDeviceProccess(container, testRunId, statuses, testCount);
  const interval = setInterval(async () => {
    await testDeviceProccess(
      container,
      testRunId,
      statuses,
      testCount,
      interval
    );
  }, 10000);

  rigAbortTests(container, testRunId);
}

async function testDeviceProccess(
  container: HTMLElement,
  testRunId: string,
  statuses: ("waiting" | "running" | "passed" | "failed" | "aborted")[],
  testCount: number,
  interval?: number
): Promise<void> {
  const test = await checkDeviceTest(testRunId);

  statuses.push(...test.tests.map((t) => t.result.status));

  setTestResult(container, test.tests, testCount);

  if (test.finishedOn || test.wasAborted) {
    if (interval) {
      clearInterval(interval);
    }
    setMainResult(container, statuses);
  }
}

function setTestResult(
  container: HTMLElement,
  tests: CheckDeviceTest[],
  testCount: number
): void {
  const logListEl = container.querySelector("#tests-log-list") as HTMLElement;
  for (const test of tests) {
    const el = document.createElement("div");
    if (test.result.status === "failed") {
      el.classList.add("test-fail");
      el.innerHTML = `( ${test.result.status} ) Test ${testCount}: ${test.name}.
       Error: ${test.result.error}`;
      continue;
    }

    el.innerHTML = `( ${test.result.status} ) Test ${testCount}: ${test.name}.`;
    logListEl.appendChild(el);
  }

  testCount++;
}

function setMainResult(
  container: HTMLElement,
  statuses: ("waiting" | "running" | "passed" | "failed" | "aborted")[]
): void {
  container.classList.add("finished");
  const testStatusEl = container.querySelector(
    "#tests-result-status"
  ) as HTMLElement;
  if (statuses.includes("failed")) {
    testStatusEl.innerHTML = "Failure";
    testStatusEl.classList.add("test-fail");
    return;
  }
  testStatusEl.innerHTML = "Success";
  testStatusEl.classList.add("test-ok");
}

async function rigAbortTests(
  container: HTMLElement,
  testRunId: string
  //   interval: number
): Promise<void> {
  const abortTestBtn = container.querySelector(
    `#${ABORT_TESTS_BTN_ID}`
  ) as HTMLElement;

  abortTestBtn.addEventListener("click", async () => {
    // clearInterval(interval);
    await fetch(`${URL}?testRunId=${testRunId}&abort=1`);
    container
      .querySelector(`#${TESTS_LOG_CONTINER_ID}`)!
      .classList.add("finished");
  });
}

async function checkDeviceTest(
  testRunId: string
): Promise<CheckDeviceTestResponse> {
  return (await (
    await fetch(`${URL}?testRunId=${testRunId}`)
  ).json()) as CheckDeviceTestResponse;
}
