import { ControlLoadError } from "../errors";
import { loadStateData } from "../data/load";
import { updateStateImg } from "./img";
import { updateStateTable } from "./table";
import { delay } from "../help";
import { hideGlobalSpinner } from "../spinner";
import { updateFooter } from "./footer";
import { setStaticData } from "./static";
import { setSticker } from "./sticker";

const UPDATE_INTERVAL = 1000;
let isRequestActive = false;
let lastRuntimeVal: number;

export async function initState(): Promise<void> {
  const errorControl = new ControlLoadError(3);
  await setStaticData(errorControl);
  await updateState(errorControl);
  // await setSticker();
  rigUpdateState(errorControl);
  rigShowPrivateKey();
}

async function updateState(errorControl: ControlLoadError): Promise<void> {
  if (sessionStorage.getItem("isReboot")) {
    await delay(5000);
  }

  let data;
  try {
    data = await loadStateData(errorControl);
  } catch (e) {
    const text = (e as Error).message;
    errorControl.showError("load", text);
    return;
  }

  if (lastRuntimeVal && lastRuntimeVal !== data.runtimeStartTimestamp) {
    window.location.reload();
  }

  if (sessionStorage.getItem("isReboot")) {
    errorControl.hideError();
    hideGlobalSpinner();
    sessionStorage.removeItem("isReboot");
  }

  updateStateImg(data);
  updateFooter(data);
  updateStateTable(data);

  lastRuntimeVal = data.runtimeStartTimestamp;
}

function rigUpdateState(errorControl: ControlLoadError): void {
  window.setInterval(async () => {
    if (isRequestActive) {
      return;
    }
    isRequestActive = true;
    await updateState(errorControl);
    isRequestActive = false;
  }, UPDATE_INTERVAL);
}

