function rigShowPrivateKey(): void {
  const btn = document.querySelector("#show-private-key-btn") as HTMLElement;
  if (!btn) {
    return;
  }
  btn.addEventListener("click", () => {
    document.querySelector("#wg-private-key")!.classList.toggle("show");
  });
}
