import { getBaseUrl, numberToHex8 } from "../help";
import { loadSettingsData, loadStateData } from "../data/load";
import {
  ModbusInterfaces,
  Rs485Modes,
  SettingsData,
  StateData,
} from "../data/types";
import {
  getAsciiRequest,
  getRtuRequest,
  getTcpRequest,
  initRequestConstructor,
} from "./constructor";
import * as moment from "moment";
import { ControlLoadError } from "../errors";

const MODBUS_CONTAINER_ID = "modbus-container";
export const INTERFACE_OPTIONS_SELECT_ID = "interface-select";
export const MODBUS_REQUESTS_TEXT_ID = "modbus-request";
const MODBUS_FORM_ID = "modbus-req-form";
const RESPONSES_CONTAINER_ID = "responses-container";
const RESPONSES_LIST_ID = "responses-list";
const ERROR_REQUEST_ID = "error-request";
const CLEAR_LOG_BTN_ID = "clear-log-btn";
const logDateFormat = "YYYY-MM-DD HH:mm:ss";

const URL = `${getBaseUrl()}/api/mbgate.json`;

type ResponseData =
  | {
      response: string;
    }
  | {
      error_code: number;
      error_descr: string;
    };

export enum ModbusCommand {
  ReadCoils = "0x01",
  ReadDiscreteInputs = "0x02",
  ReadHoldingRegisters = "0x03",
  ReadInputRegisters = "0x04",
  WriteSingleCoil = "0x05",
  WriteSingleRegister = "0x06",
  WriteMultipleCoils = "0x0F",
  WriteMultipleRegisters = "0x10",
}

export enum RequestType {
  Opcb = "opcb",
  Mbtcp0 = "mbtcp0",
  Mbtcp1 = "mbtcp1",
  Rs4850 = "rs4850",
  Rs4851 = "rs4851",
}

export async function initModbus(): Promise<void> {
  const container = document.querySelector(
    `#${MODBUS_CONTAINER_ID}`
  ) as HTMLElement;
  if (!container) {
    return;
  }
  const errorControl = new ControlLoadError();
  const settingsData = await loadSettingsData(errorControl);
  const stateData = await loadStateData(errorControl);

  initRequest(container, settingsData, stateData);
  initRequestConstructor(container, settingsData);
}

function initRequest(
  container: HTMLElement,
  settingsData: SettingsData,
  stateData: StateData
): void {
  rigChangeInterfaceType(container);
  rigClearLogBtn(container);
  rigSubmitForm(container, settingsData);
  setIntarfaceOptions(container, settingsData, stateData);
}

function rigChangeInterfaceType(container: HTMLElement): void {
  const selectEl = container.querySelector(
    `#${INTERFACE_OPTIONS_SELECT_ID}`
  ) as HTMLSelectElement;
  const reuestsField = container.querySelector(
    `#${MODBUS_REQUESTS_TEXT_ID}`
  ) as HTMLTextAreaElement;
  selectEl.addEventListener("change", () => {
    reuestsField.value = "";
  });
}

function rigSubmitForm(
  container: HTMLElement,
  settingsData: SettingsData
): void {
  const form = container.querySelector(`#${MODBUS_FORM_ID}`) as HTMLFormElement;
  form.addEventListener("submit", async (e) => {
    e.preventDefault();
    await submitForm(form, settingsData);
  });
}

function rigClearLogBtn(container: HTMLElement): void {
  const btn = container.querySelector(`#${CLEAR_LOG_BTN_ID}`);
  btn?.addEventListener("click", () => {
    (
      container.querySelector(`#${RESPONSES_LIST_ID}`) as HTMLTextAreaElement
    ).innerHTML = "";
  });
}

async function submitForm(
  form: HTMLElement,
  settingsData: SettingsData
): Promise<void> {
  const requests = parseRequestsText(
    (form.querySelector(`#${MODBUS_REQUESTS_TEXT_ID}`) as HTMLTextAreaElement)
      .value,
    form
  );
  if (!requests || requests.length < 1) {
    return;
  }

  const { type, path } = getInterfaceTypeAndPath(form, settingsData);

  form.querySelector(`#${RESPONSES_CONTAINER_ID}`)?.classList.remove("d-none");

  let lastTcpTransId: undefined | number;
  for (const request of requests) {
    let reqBody = "";
    if (type === "tcp") {
      reqBody = getTcpBodyFromReq(request);
      lastTcpTransId = lastTcpTransId === undefined ? 0 : lastTcpTransId + 1;
    } else if (type === "ascii") {
      reqBody = getAsciiBodyFormReq(request);
    } else {
      reqBody = getRtuBodyFromReq(request);
    }

    const reqTime = new Date().getTime();
    const response = await fetch(`${URL}?request=${reqBody}&dst=${path}`);
    const resTime = new Date().getTime();
    const result = JSON.parse(await response.text()) as ResponseData;

    if ("error_code" in result) {
      showResonse(
        request.map(numberToHex8).join(":"),
        result.error_descr,
        reqTime,
        resTime
      );
    } else {
      showResonse(
        request.map(numberToHex8).join(":"),
        getResponseBytesStr(result.response, type, lastTcpTransId),
        reqTime,
        resTime
      );
    }
  }
}

function showResonse(
  request: string,
  responseResult: string,
  reqTime: number,
  resTime: number
): void {
  const respListEl = document.querySelector(`#${RESPONSES_LIST_ID}`)!;
  const item = document.createElement("div");
  item.classList.add("response-item");
  const reqTimeStr = moment(reqTime).format(
    `${logDateFormat}.${new Date(reqTime)
      .getMilliseconds()
      .toString()
      .padStart(3, "0")}`
  );
  const resTimeStr = moment(resTime).format(
    `${logDateFormat}.${new Date(resTime)
      .getMilliseconds()
      .toString()
      .padStart(3, "0")}`
  );
  item.innerHTML = `${reqTimeStr} > ${request} </br>
  ${resTimeStr} < ${responseResult}`;
  respListEl.appendChild(item);
}

function parseRequestsText(
  text: string,
  form: HTMLElement
): number[][] | undefined {
  const rawReqs = text
    .split("\n")
    .filter((s) => !s.startsWith("#") && s.trim() !== "");

  const reqTextEl = form.querySelector(`#${MODBUS_REQUESTS_TEXT_ID}`)!;
  const errorTextEl = form.querySelector(`#${ERROR_REQUEST_ID}`)!;
  const cleanReqs = [];
  for (const req of rawReqs) {
    const cleanReq = req.replace(/\W/g, "").toLowerCase();
    // check a-f
    const errorCharacters = cleanReq.match(/[^a-f0-9]/g);
    if (errorCharacters?.length) {
      reqTextEl.classList.add("is-invalid");
      errorTextEl.innerHTML = `Error request: ${req}. The request cannot contain: ${errorCharacters.join(
        ","
      )}`;
      return;
    }
    // check parity
    if (cleanReq.length % 2) {
      reqTextEl.classList.add("is-invalid");
      errorTextEl.innerHTML = `Error request: ${req}. The request must have an even number of bytes.}`;
      return;
    }
    cleanReqs.push(cleanReq);
  }
  errorTextEl.innerHTML = "";
  reqTextEl.classList.remove("is-invalid");

  const result: number[][] = [];
  for (let reqStr of cleanReqs) {
    const bytes: number[] = [];
    while (reqStr.length > 0) {
      bytes.push(Number(`0x${reqStr.slice(0, 2)}`));
      reqStr = reqStr.substring(2);
    }
    result.push(bytes);
  }
  return result;
}

function getResponseBytesStr(
  response: string,
  reqType: "tcp" | "rtu" | "ascii",
  lastTcpTransId?: number
): string {
  const bytes: string[] = [];
  while (response.length > 0) {
    bytes.push(response.slice(0, 2));
    response = response.substring(2);
  }
  let result = "";
  const numbers = bytes.map((b) => Number(`0x${b}`));
  if (reqType === "tcp") {
    result = getTcpRequest(numbers, lastTcpTransId || 0);
  } else if (reqType === "ascii") {
    result = getAsciiRequest(numbers);
  } else {
    result = getRtuRequest(numbers);
  }
  return result;
}

function setIntarfaceOptions(
  container: HTMLElement,
  settingsData: SettingsData,
  stateData: StateData
): void {
  const selectEl = container.querySelector(
    `#${INTERFACE_OPTIONS_SELECT_ID}`
  ) as HTMLSelectElement;

  if (settingsData?.virtualEnabled) {
    const option = document.createElement("option");
    option.value = "opcb||self";
    option.text = "OPCB-221 (Modbus TCP)";
    selectEl.appendChild(option);
  }

  for (let i = 0; i <= 3; i++) {
    const type = settingsData[`mbtcpCli${i}NetifType`] as ModbusInterfaces;
    const netif = stateData[`mbtcpCli_${i}Netif`] as number;
    if (!type || (netif !== 2 && netif !== 3)) {
      continue;
    }
    const option = document.createElement("option");
    const ipPort = `${settingsData[`mbtcpCli${i}RemoteServerIp`]}:${
      settingsData[`mbtcpCli${i}RemoteServerPort`]
    }`;
    const mode = netif === 2 ? "Ethernet" : "WIFI";
    option.value = `mbtcp||${ipPort}`;
    option.text = `Modbus Client: ${mode} ${ipPort}`;
    selectEl.appendChild(option);
  }

  for (let i = 0; i <= 3; i++) {
    const devName = settingsData[`rs485${i}DevName`] as string | undefined;
    const mode = settingsData[`rs485${i}Mode`] as Rs485Modes;
    if (!devName || (mode !== 2 && mode !== 3)) {
      continue;
    }
    const option = document.createElement("option");
    option.value = `rs485||${devName}`;
    option.text = `${settingsData?.rs4850DevName} (${getRS485ModeName(mode)})`;
    selectEl.appendChild(option);
  }
}

function getTcpBodyFromReq(request: number[]): string {
  const [_tid0, _tid1, _pid0, _pid1, _len0, _len1, ...body] =
    request.map(numberToHex8);
  return body.join("");
}

function getRtuBodyFromReq(request: number[]): string {
  // remove crc
  return request.slice(0, -2).map(numberToHex8).join("");
}

function getAsciiBodyFormReq(request: number[]): string {
  // remove lrc, :, \r\n
  const hex = request.slice(0, -4).slice(1).map(numberToHex8);
  let result = "";
  for (const code of hex) {
    result += String.fromCharCode(Number(`0x${code}`));
  }
  return result;
}

function getRS485ModeName(val: Rs485Modes): string {
  if (val === 0) {
    return "Modbus RTU Master";
  }
  return "Modbus ASCII Master";
}

export function getInterfaceTypeAndPath(
  container: HTMLElement,
  settingsData: SettingsData
): { type: "tcp" | "rtu" | "ascii"; path: string } {
  const [reqType, path] = (
    container.querySelector(
      `#${INTERFACE_OPTIONS_SELECT_ID}`
    ) as HTMLSelectElement
  ).value.split("||");

  if (
    reqType === RequestType.Opcb ||
    reqType === RequestType.Mbtcp0 ||
    reqType === RequestType.Mbtcp1
  ) {
    return {
      type: "tcp",
      path,
    };
  }

  if (
    (reqType === RequestType.Rs4850 && settingsData.rs4850Mode === 1) ||
    (reqType === RequestType.Rs4851 && settingsData.rs4851Mode === 1)
  ) {
    return { type: "ascii", path };
  }

  return { type: "rtu", path };
}
