import { isValidIp } from "../../help";
import JustValidate from "just-validate";

export function validateForm(form: HTMLFormElement): JustValidate {
  const validation = new JustValidate(form, {
    errorFieldCssClass: "is-invalid",
    focusInvalidField: true,
  });
  validation
    .addField("#password", [
      {
        rule: "maxLength" as any,
        value: 32,
      },
      {
        rule: "required" as any,
      },
    ])
    .addField("#hostname", [
      {
        rule: "maxLength" as any,
        value: 64,
      },
      {
        rule: "minLength" as any,
        value: 5,
      },
      {
        validator: (value): boolean => {
          return typeof value === "boolean"
            ? value
            : value.match(/([0-9a-zA-Z-f])/g)!.length < value.length
            ? false
            : true;
        },
        errorMessage:
          "Can contain only: letters from a to z, the digits from 0 to 9, and the hyphen (-).",
      },
      {
        rule: "required" as any,
      },
    ])
    // Ethernet
    .addField("#ethernet-netif-name", [
      {
        rule: "maxLength" as any,
        value: 20,
      },
      {
        rule: "required" as any,
      },
    ])
    .addField("#ethernet-ip", [
      {
        validator: (value): boolean =>
          typeof value === "string" ? isValidIp(value) : value,
        errorMessage: "Invalid IPv4 address.",
      },
      {
        rule: "required" as any,
      },
    ])
    .addField("#ethernet-mask", [
      {
        validator: (value): boolean =>
          typeof value === "string" ? isValidIp(value) : value,
        errorMessage: "Invalid ethernet mask.",
      },
      {
        rule: "required" as any,
      },
    ])
    .addField("#ethernet-getway", [
      {
        validator: (value): boolean =>
          typeof value === "string" ? isValidIp(value) : value,
        errorMessage: "Invalid IPv4 address.",
      },
      {
        rule: "required" as any,
      },
    ])
    .addField("#ethernet-dns-ip", [
      {
        validator: (value): boolean =>
          typeof value === "string" ? isValidIp(value) : value,
        errorMessage: "Invalid IPv4 address.",
      },
    ])
    .addField("#ethernet-second-dns-ip", [
      {
        validator: (value): boolean =>
          typeof value === "string" ? isValidIp(value) : value,
        errorMessage: "Invalid IPv4 address.",
      },
      {
        rule: "required" as any,
      },
    ])
    // WIFI
    // TODO: check ssid password min max
    .addField("#wifi-netif-name", [
      {
        rule: "maxLength" as any,
        value: 20,
      },
    ])
    .addField("#wifi-ap-ssid", [
      {
        rule: "maxLength" as any,
        value: 32,
      },
      {
        rule: "minLength" as any,
        value: 2,
      },
    ])
    .addField("#wifi-custom-ip", [
      {
        validator: (value): boolean =>
          typeof value === "string" ? isValidIp(value) : value,
        errorMessage: "Invalid IPv4 address.",
      },
      {
        rule: "required" as any,
      },
    ])
    .addField("#wifi-custom-mask", [
      {
        validator: (value): boolean =>
          typeof value === "string" ? isValidIp(value) : value,
        errorMessage: "Invalid IPv4 address.",
      },
      {
        rule: "required" as any,
      },
    ])
    .addField("#wifi-custom-getway", [
      {
        validator: (value): boolean =>
          typeof value === "string" ? isValidIp(value) : value,
        errorMessage: "Invalid IPv4 address.",
      },
      {
        rule: "required" as any,
      },
    ])
    .addField("#wifi-dns-ip", [
      {
        validator: (value): boolean =>
          typeof value === "string" ? isValidIp(value) : value,
        errorMessage: "Invalid IPv4 address.",
      },
      {
        rule: "required" as any,
      },
    ])
    .addField("#wifi-second-dns-ip", [
      {
        validator: (value): boolean =>
          typeof value === "string" ? isValidIp(value) : value,
        errorMessage: "Invalid IPv4 address.",
      },
      {
        rule: "required" as any,
      },
    ])
    // Overvis
    .addField("#overvis-remote-host-name", [
      {
        rule: "maxLength" as any,
        value: 64,
      },
      {
        rule: "required" as any,
      },
    ])
    // Modbus TCP
    .addField("#modbus-server-port", [
      {
        rule: "minNumber" as any,
        value: 1,
      },
      {
        rule: "maxNumber" as any,
        value: 65535,
      },
      {
        rule: "required" as any,
      },
    ])
    .addField("#modbus-server-res-timeout", [
      {
        rule: "minNumber" as any,
        value: 1,
      },
      {
        rule: "maxNumber" as any,
        value: 60000,
      },
      {
        rule: "required" as any,
      },
    ])
    .addField("#modbus-server-simultaneous", [
      {
        rule: "minNumber" as any,
        value: 1,
      },
      {
        rule: "maxNumber" as any,
        value: 20,
      },
      {
        rule: "required" as any,
      },
    ])
    // Modbus getway
    .addField("#getway-device-id", [
      {
        rule: "minNumber" as any,
        value: 0,
      },
      {
        rule: "maxNumber" as any,
        value: 255,
      },
      {
        rule: "required" as any,
      },
    ])
    // Modbus RS-485
    .addField("#rs-485-device-name", [
      {
        rule: "maxLength" as any,
        value: 20,
      },
    ])
    .addField("#rs-485-response-time", [
      {
        rule: "minNumber" as any,
        value: 1,
      },
      {
        rule: "maxNumber" as any,
        value: 60000,
      },
      {
        rule: "required" as any,
      },
    ])
    .addField("#rs-485-1-device-name", [
      {
        rule: "maxLength" as any,
        value: 20,
      },
    ])
    .addField("#rs-485-1-response-time", [
      {
        rule: "minNumber" as any,
        value: 1,
      },
      {
        rule: "maxNumber" as any,
        value: 60000,
      },
      {
        rule: "required" as any,
      },
    ])
    .addField("#modbus-client-route-uid", [
      {
        validator: (value): boolean =>
          typeof value === "string" ? isValidRouteUids(value) : value,
        errorMessage: "Invalid input. Example: 0-25,50-255",
      },
    ]);

  return validation;
}

function isValidRouteUids(value: string): boolean {
  const limits = value.split(/\,/);
  if (limits.length < 1) {
    return false;
  }

  for (const limit of limits) {
    const valuesArrStr = limit.split("-");
    if (valuesArrStr.length < 1 || valuesArrStr.length > 2) {
      return false;
    }
    const [low, high] = valuesArrStr.map(Number);
    if (
      Number.isNaN(low) ||
      Number.isNaN(high) ||
      high < low ||
      low < 0 ||
      low > 255 ||
      high > 255 ||
      high < 0
    ) {
      return false;
    }
  }

  return true;
}
