import { FinalizeDeviceResponse } from "../data/types";
import {
  MessageKind,
  showAfterUpdatePageMessage,
  showPageMessage,
} from "../alert";
import { getBaseUrl } from "../help";
import { hideGlobalSpinner, showGlobalSpinner } from "../spinner";
import { showLoadError } from "../errors";

const MF_FINALIZE_BTN_ID = "mf-finalize-btn";
const URL = `${getBaseUrl()}/api/mf_close.json`;

export function initFinalize(container: HTMLElement): void {
  const containerFinalize = document.querySelector(
    "#finalize-form-sector"
  ) as HTMLElement;
  if (!containerFinalize.classList.contains("show")) {
    return;
  }
  const btn = container.querySelector(`#${MF_FINALIZE_BTN_ID}`) as HTMLElement;
  btn.addEventListener("click", async () => {
    const conf = confirm("Do you want to finalize device?");
    if (!conf) {
      return;
    }

    await submit();
  });
}

async function submit(): Promise<void> {
  showGlobalSpinner();

  try {
    const resp = JSON.parse(
      await (await fetch(URL)).text()
    ) as FinalizeDeviceResponse;

    if (Number(resp.error_code)) {
      showPageMessage(
        resp.error_descr || "Undefined error.",
        MessageKind.Danger
      );
      return;
    }
  } catch (e) {
    const text = (e as Error).message;
    hideGlobalSpinner();
    showLoadError(text);
    return;
  }

  // if success
  showAfterUpdatePageMessage(
    "The device finalized successfully",
    MessageKind.Success
  );
  window.location.reload();
}
