import { showGlobalSpinner } from "../spinner";
import { getBaseUrl } from "../help";

const DEACTIVATE_CODE_BTN_ID = "unlink-btn";
const URL = `${getBaseUrl()}/api/cmd.json?cmd_ovb_deactivate`;

export function rigDeactivateCode(): void {
  document
    .querySelector(`#${DEACTIVATE_CODE_BTN_ID}`)
    ?.addEventListener("click", async () => {
      const conf = confirm("Do you want deactivate code?");
      if (!conf) {
        return;
      }
      await deactivateCode();
    });
}

async function deactivateCode(): Promise<void> {
  showGlobalSpinner(true);
  sessionStorage.setItem("isReboot", "true");

  await fetch(URL);
}
