import { hideGlobalSpinner, showGlobalSpinner } from "../spinner";
import { RestoreDeviceResponse, StateData, StaticData } from "../data/types";
import { getBaseUrl } from "../help";
import {
  MessageKind,
  showAfterUpdatePageMessage,
  showPageMessage,
} from "../alert";
import { showLoadError } from "../errors";

const RESTORE_FORM_ID = "restore-identity-form";
const RESTORE_SBMT_BTN_ID = "restore-sbmt-btn";
const URL = `${getBaseUrl()}/api/mf_reg.json`;

export function initRestoreIdentity(
  container: HTMLElement,
  _data: StaticData,
  stateData: StateData
): void {
  const form =
    (container.querySelector(`#${RESTORE_FORM_ID}`) as HTMLFormElement) || null;
  if (!form || !form.parentElement?.classList.contains("show")) {
    return;
  }
  checkInternetConnection(stateData.inetState, form);
  checkWireguardConnection(stateData.wgState);
  rigSubmit(form);
}

function checkInternetConnection(
  inetState: number,
  form: HTMLFormElement
): void {
  if (inetState) {
    return;
  }
  showPageMessage(
    `No internet access. Active internet connection is required to restore the device's identity. 
    Please check the device's settings.`,
    MessageKind.Danger
  );
  form
    .querySelector(`#${RESTORE_SBMT_BTN_ID}`)!
    .setAttribute("disabled", "disabled");
}

function checkWireguardConnection(wgState: number): void {
  if (wgState === 3) {
    showPageMessage(
      `This device is already authenticated on the remote server and has an access. 
      By submitting this form you may lose the access or change the device's identity.`,
      MessageKind.Warning
    );
  }
}

function rigSubmit(form: HTMLFormElement): void {
  form.addEventListener("submit", async (e) => {
    e.preventDefault();
    const conf = confirm("Do you want to restore device?");
    if (!conf) {
      return;
    }
    await submit(form);
  });
}

async function submit(form: HTMLFormElement): Promise<void> {
  hideFormError(form);

  showGlobalSpinner(true);

  const devKey = (form.querySelector("#private-key") as HTMLInputElement).value;
  if (!devKey) {
    showFormError(form, "Required field.");
  }
  try {
    const resp = JSON.parse(
      await (await fetch(`${URL}?dev_key=${devKey}`)).text()
    ) as RestoreDeviceResponse;

    if (Number(resp.error_code)) {
      showPageMessage(
        "Form submit error. Please try again.",
        MessageKind.Danger
      );
      showFormError(
        form,
        resp.error_descr || "Undefined error. Please try again."
      );
      hideGlobalSpinner();
      return;
    } else {
      showAfterUpdatePageMessage(
        "The device has successfully authenticated on the remote server with the provided identity.",
        MessageKind.Success
      );
    }

    sessionStorage.setItem("isReboot", "true");
  } catch (e) {
    const text = (e as Error).message;
    showLoadError(text);
    return;
  }
}

function showFormError(form: HTMLFormElement, errorText: string): void {
  const inputEl = form.querySelector("#private-key") as HTMLInputElement;
  inputEl.classList.add("is-invalid");
  form.querySelector(".invalid-feedback")!.innerHTML = errorText;
}

function hideFormError(form: HTMLFormElement): void {
  const inputEl = form.querySelector("#private-key") as HTMLInputElement;
  inputEl.classList.remove("is-invalid");
  form.querySelector(".invalid-feedback")!.innerHTML = "";
}
