import { setNewValue } from ".";
import { StateData } from "../../data/types";

enum LanStatus {
  disabled = "Disabled",
  disconnected = "Disconnected",
  offline = "Offline",
  online = "Online",
}

export function updateElanTable(stateData: StateData): void {
  updateElanIp(
    stateData.elanIp,
    stateData.elanGateway,
    stateData.elanMaskPrefix
  );
  updateElanStatus(stateData.elanEnabled, stateData.elanState);
}

function updateElanStatus(isEnable: number, state: number): void {
  const el = document.querySelector("#lan-status");
  if (!el) {
    return;
  }
  if (!isEnable) {
    el.querySelector(".value")!.innerHTML = LanStatus.disabled;
    return;
  }

  if (!state || state === 1) {
    el.querySelector(".value")!.innerHTML = LanStatus.disconnected;
    return;
  } else if (state === 2) {
    el.querySelector(".value")!.innerHTML = LanStatus.offline;
    return;
  }

  el.querySelector(".value")!.innerHTML = LanStatus.online;
}

function updateElanIp(
  elanIp: string,
  elanGateway: string,
  elanMaskPrefix: number
): void {
  const el = document.querySelector("#lan-ip");
  if (!el) {
    return;
  }
  setNewValue(
    el.querySelector(".value")!,
    `${elanIp}/${elanMaskPrefix} (gate: ${elanGateway})`
  );
}
